import ComputerData from './computerdata';
import Buttons from './buttons';
import apiFetch from '@wordpress/api-fetch';

const { Component } = wp.element;
import { Spinner } from '@wordpress/components';

const assetsURL = '/wp-content/plugins/strava-watts/assets/';

class App extends Component {
	constructor( props ) {
		super( props );
		this.state = {
			athleteData: [],
			loading: true,
			view: 'week',
			displayText: 'This Week',
			viewTypes: ['year', 'week', 'month'],
		};
		
		this.getApiUrl = this.getApiUrl.bind( this );
	}

	componentDidMount() {
		this.runApiFetch();
	}
	
	runApiFetch() {
		wp.apiFetch( {
			path: this.getApiUrl(),
		} ).then( ( data ) => {
			this.setState( {
				athleteData: data,
				loading: false,
			} );
		} );
	}	
	
	getApiUrl() {
		const params = [];
		const view = 'default';

		let apiURL = 'stwatt/v1/athlete';
		
        switch(view) {
            case 'year':
                // code block
                break;
            case 'month':
                // code block
                break;
            case 'week':
                this.setState( { 
                    displayText: 'This Week' 
                } );
                apiURL = apiURL;
        }		

console.log('getApiUrl()');
console.log('view: ' + this.state.view);

		return apiURL;
	}	

	render() {
		return (
			<div id="computer-wrapper" className="computer-wrapper">
			    <div id="computer" className="computer">
    				{ this.state.loading ? (
    					<Spinner />
    				) : (
    				    <ComputerData stats={ this.state.athleteData.stats } displayText = { this.state.displayText } />
    				) }
    				<Buttons view={this.state.view} viewTypes = { this.state.viewTypes } />
                    <div className="powered-by">
        				<img
        					src={ assetsURL + 'images/pb-strava-horz-color.png' }
        					alt="powered by strava"
        				/>
        			</div>
    			</div>
            </div>
		);
	}
}

export default App;